/* Add styles for the PostList component */
.post-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
  padding: 20px;
}

.post {
  /* Updated styles to remove white space */
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100%;
}

/* Add styles to remove white space around images */
.post img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 5px;
}

.post h2 {
  margin-top: 10px;
  font-size: 24px;
}

.post p {
  margin-top: 0px;
}

.transparent-h2 {
  background-color: transparent;
}

.post.home {
  display: flex;
  align-items: center; /* Vertically center the content */
  justify-content: center; /* Horizontally center the content */
  height: 100%; /* Ensure the container takes the full height */
}

